import { musicAPI } from "../api/music-api";
import { InferActionsTypes, ThunkType } from "./redux-store";


type PhotosType = {
    small: null | string,
    large: null | string
}

type MusicType = {
    name: string,
    id: number,
    photos: PhotosType,
    status: string | null,
    followed: boolean
}

const initialState = {
    musicPop: [] as MusicType[],
    pageSize: 5,
    currentPage: 1,
    filter: {
        term: '',
        friend: null as boolean | null
    },
    isFaching: false
};

type InitialStateType = typeof initialState;
type FilterType = typeof initialState.filter

const musicReducer = (state = initialState, action:ActionsTypes ):InitialStateType => {
    switch(action.type) {
        case "music/MUSIC-POP-RECIVED":
            return {
                ...state,
                musicPop: [...action.payload.musicPop]
            }
        case "music/MUSIC-TOGGLE-IS-FACHING":
            return {
                ...state,
                ...action.payload
            }
        default:
            return state
    }
}





type ActionsTypes = InferActionsTypes<typeof actions>;

const actions = {
    musicPopRecived: (musicPop: MusicType[]) => ({type: 'music/MUSIC-POP-RECIVED', payload: {musicPop}} as const),
    musicToggleIsFaching: (isFaching: boolean) => ({type: 'music/MUSIC-TOGGLE-IS-FACHING', payload: {isFaching}} as const)
};




type ThunkTypeMusic = ThunkType<ActionsTypes>;


export const requestMusicPop = (pageSize: number, currentPage: number, filter: FilterType):ThunkTypeMusic => async (dispatch) => {
    try{
        dispatch(actions.musicToggleIsFaching(true))
        let data = await musicAPI.getMusicPop(pageSize, currentPage, filter.term, filter.friend);
        let {items} = data;
        dispatch(actions.musicPopRecived(items))
        dispatch(actions.musicToggleIsFaching(false))
    }catch(error) {
    }
};




export default musicReducer;