import { useEffect } from "react";
import { useAppDispatch, useAppSelector } from "../../redux/redux-store";
import { requestMusicPop } from "../../redux/music-reducer";
import { Music } from "./Music";
import Preloader from "../common/Preloader/Preloader";


const Musics = () => {
    const dispatch = useAppDispatch()
    const musicPop = useAppSelector(state => state.musicPage.musicPop);
    const pageSize = useAppSelector(state => state.musicPage.pageSize);
    const currentPage = useAppSelector(state => state.musicPage.currentPage);
    const filter = useAppSelector(state => state.musicPage.filter);
    const isFaching = useAppSelector(state => state.musicPage.isFaching);

    useEffect(() => {
        dispatch(requestMusicPop(pageSize, currentPage, filter))
    }, [])

    return (
        <>
            {isFaching && <Preloader />}
            {
                musicPop.map(m => <Music {...m} />)
            }
        </>
    )
};


export default Musics;