import musicPhoto from '../../assets/images/user.jpg'

type PhotosType = {
    small: null | string,
    large: null | string
}

type PropsType = {
    name: string,
    id: number,
    photos: PhotosType,
    status: string | null,
    followed: boolean
}


export const Music = ({photos, name}: PropsType) => {
    if(!photos) return null;

    return (
        <>
            <div style={{maxWidth: '100px'}}>
                <img style={{maxWidth: '100%'}} src={photos.large ?? musicPhoto} alt="avatar" />
            </div>
            <div>{name}</div>
        </>
    )
};

